// ---------------------------------------------------------- MAIN MENU
// ----------------------------------------------------------

var mainMenu = function(){

  //---------------------------------------------------------
  // EVENTI AL CLICK SU BURGER

  $('button[data-bs-target="#navbarNavDropdown"]').click(function(e){
    $('body, html').toggleClass('menu-open');
    $(this).toggleClass('is-active');
  });


  //---------------------------------------------------------
  // RULER PADDING DROPDOWN - gestito fino a 2 righe di voci
  if($(window).width() > 991){
    function dropdownGap(){
      if( $('#main-menu').outerHeight() > $('#main-menu > li').outerHeight() ){
        var menuGap = ( ( $('#navbarNavDropdown').outerHeight() - $('#main-menu').outerHeight() ) / 2 ) + ($('#main-menu > li').outerHeight()) +1;
      } else {
        var menuGap = ( ( $('#navbarNavDropdown').outerHeight() - $('#main-menu').outerHeight() ) / 2 ) + 1;
      }
      return menuGap;
    }
    $('#main-menu > li > a.dropdown-toggle').next().css('padding-top', dropdownGap());
  } else {
    $('#main-menu > li > a.dropdown-toggle').next().removeAttr('style');
  }
}



// ---------------------------------------------------------- MOBILE MENU
// ----------------------------------------------------------

var mobileMenu = function() {
  // language & social in mobile menu

  $("#lang-sw").children().clone(true,true).appendTo('#mobile-social-lang');
  $("#social-header").children().clone(true,true).appendTo('#mobile-social-lang');

}


// ---------------------------------------------------------- STICKY HANDLER
// ----------------------------------------------------------

var stickyHandler = function() {
  // language & social in mobile menu
  var headerHeight = $('header#wrapper-navbar').height();
  // $('#detail-sidebar .sticky-top').css('top', headerHeight - 16 + 'px'); // cos va a 0 sull'header
  $('#detail-sidebar .sticky-top').css('top', headerHeight + 64 + 'px');
}


// ---------------------------------------------------------- LAYOUT
// ----------------------------------------------------------

// FULL PAGE
// fullPage -> l'elemento con attributo 'fs-page' prende l'altezza corrente della finestra
// se passato un valore viene tolta l'altezza dell'elemento a cui punta
// es. <section id="home-1" fs-page="header#wrapper-navbar">
var fullPage = function() {
  var winHeight = window.innerHeight;
  var headerHeight = $('header#wrapper-navbar').outerHeight() + parseInt($('header#wrapper-navbar').css('margin-bottom'), 10);
  var footerHeight = $('footer.site-footer').outerHeight() + parseInt($('footer.site-footer').css('margin-top'), 10);

  $('[fs-page]').each(function(){
    var noHeader = winHeight - headerHeight;
    var noFooter = winHeight - footerHeight;
    var content = winHeight - headerHeight - footerHeight;
    var param = $(this).attr('fs-page');

    // Compatibilit responsive es. per blocco highlight
    if(param.lenght !== 0){
      if(param === 'no-header'){
        $(this).css('height', noHeader);
      }
      if(param === 'no-footer'){
        $(this).css('height', noFooter);
      }
      if(param === 'content'){
        $(this).css('height', content);
      }
    } else {
      $(this).css('height', winHeight);
    }
    //
    // $(this).css('padding-top', headerHeight);

    // Classe che identifica la sezione pronta
    $(this).addClass('computed').css('opacity', '1');
  });

}


export {
  mainMenu,
  mobileMenu,
  stickyHandler,
  fullPage
};
