import * as basicLightbox from 'basiclightbox/src/scripts/main.js';

  // ---------------------------------------------------------- URL PARAMS
  // ----------------------------------------------------------
  // Restituisce parametro url
  // Uso:
  // http://testsite.dvl/?filtro=valore
  // RwUtil.getUrlParam('filtro');
  // restituisce 'valore'

  var getUrlParam = function (sParam) {
      var sPageURL = window.location.search.substring(1),
          sURLVariables = sPageURL.split('&'),
          sParameterName,
          i;

      for (i = 0; i < sURLVariables.length; i++) {
          sParameterName = sURLVariables[i].split('=');

          if (sParameterName[0] === sParam) {
              return sParameterName[1] === undefined ? true : decodeURIComponent(sParameterName[1]);
          }
      }
  };

  var cleanUrlParam = function () {
    var uri = window.location.toString();
    if (uri.indexOf("?") > 0) {
        var clean_uri = uri.substring(0, uri.indexOf("?"));
        window.history.replaceState({}, document.title, clean_uri);
    }
  }


  // ---------------------------------------------------------- CURSOR/ELEMENT DISTANCE CALCULATOR
  // ---------------------------------------------------------- Usato nel menu flottante per attivare la parola in prossimit della lettera
  // usato in assets/scripts/classi/navigation.js

  var getCursorDistance = function (e, element) {
    var mX = e.pageX;
    var mY = e.pageY;
    var from = {x:mX, y:mY};
    var $n=$(element);
    var off = $n.offset(),
        nx1 = off.left,
        ny1 = off.top,
        nx2 = nx1 + $n.outerWidth(),
        ny2 = ny1 + $n.outerHeight(),
        maxX1 = Math.max(mX, nx1),
        minX2 = Math.min(mX, nx2),
        maxY1 = Math.max(mY, ny1),
        minY2 = Math.min(mY, ny2),
        intersectX = minX2 >= maxX1,
        intersectY = minY2 >= maxY1,
        to = {
            x: intersectX ? mX : nx2 < mX ? nx2 : nx1,
            y: intersectY ? mY : ny2 < mY ? ny2 : ny1
        };
    var distX = to.x - from.x,
        distY = to.y - from.y,
        hypot = Math.sqrt(distX * distX + distY * distY);
    return hypot;
  }


  // ---------------------------------------------------------- COUNTERS
  // ----------------------------------------------------------
  // usato in assets/scripts/about.js

  var counters = function () {
    const progetto = document.querySelectorAll('.project-counter');
    observer = new IntersectionObserver(entries => {
      entries.forEach(entry => {
        if (entry.intersectionRatio > 0) {
          $('.project-counter').counterUp();
        } // else { console.log('out of view'); }
      });
    });
    progetto.forEach(counter => {
      observer.observe(counter);
    });
  }


  // ---------------------------------------------------------- SCROLL TO DIV
  // ----------------------------------------------------------

  var scrollTo = function (element) {

    if($('#wpadminbar').lenght){
      adminBarHeight = $('#wpadminbar').outerHeight();
    } else {
      adminBarHeight = 0;
    }

    // console.log('mutated scrollTo: ' + element);

    $('html,body').animate({
        scrollTop: $('#projects').offset().top - adminBarHeight
      }, 350);
  }


  // ---------------------------------------------------------- MUTATION OBSERVER
  // ---------------------------------------------------------- Monitora change in attr ed esegue funzione
  // es. Rwutil.onMutation($('#targetDiv'), 'class', 'nomeclasse', funzioneDaEseguire;
  // N.B. la funzione deve essere anonima es, var funzioneDaEseguire = function(){ console.log('esegui questa') }

  var onMutation = function ($div, attrName, attrValue, execFunction) {

    var observer = new MutationObserver(function(mutations) {
      mutations.forEach(function(mutation) {
        if (mutation.attributeName === attrName) {
          var attributeValue = $(mutation.target).prop(mutation.attributeName);
          // console.log("Class attribute changed to:", attributeValue);

          // Se l'attributo contiene attrValue eseguo la funzione
          if(attributeValue.indexOf(attrValue) >= 0){
            execFunction();
          }
        }
      });
    });
    observer.observe($div[0], {
      attributes: true
    });

  }


  // ---------------------------------------------------------- VIDEO EMBED
  // ----------------------------------------------------------

  var videoEmbed = function (videoElementClass1, videoElementClass2) {

    function YouTubeGetID(url){
        url = url.split(/(vi\/|v%3D|v=|\/v\/|youtu\.be\/|\/embed\/)/);
        return undefined !== url[2]?url[2].split(/[^0-9a-z_\-]/i)[0]:url[0];
    }

    // CLICK SU VIDEO PLAYER
    $(document).on('click','.play-layer',function(e){

      if($(this).closest('.' + videoElementClass1).length){
        var videoElementClass = videoElementClass1;
      } else {
        var videoElementClass = videoElementClass2;
      }

      var videoType = $(this).closest('.' + videoElementClass).attr('data-video-type');
      var videoUrl = $(this).closest('.' + videoElementClass).attr('data-video');

      if(videoType == 'file'){ // mp4 video
        var embedCode = '<video controls autoplay><source src="'+ videoUrl +'" type="video/mp4"></video>'
      } else { // youtube video
        var youtubeId = YouTubeGetID(videoUrl);
        var embedCode = '<iframe src="https://www.youtube.com/embed/'+ youtubeId +'?&autoplay=1" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>'
      }

      var caption = $(this).closest('.' + videoElementClass).find('img').attr('data-caption');
      if (caption == undefined){
        var caption = '';
      }

      e.preventDefault();

      basicLightbox.create(
        `
        <div class="d-flex justify-content-center align-items-center position-absolute w-100 h-100">
          <div class="spinner-border" style="width: 3rem; height: 3rem;" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
        <figure class="">
          <div class="rw-video">
            ${embedCode}
          </div>
          <figcaption>${caption}</figcaption>
        </figure>`,
        {
          className: 'iframe-embed',
        }
      ).show();
    });

  }



  export {
    getUrlParam,
    cleanUrlParam,
    getCursorDistance,
    counters,
    scrollTo,
    onMutation,
    videoEmbed
  }
