import * as basicLightbox from 'basiclightbox/src/scripts/main.js';
import * as RwSliders from '@scripts/classi/sliders.js';
import * as RwUtil from '@scripts/classi/utility.js';


jQuery(document).ready(function($) {
  if($('body').hasClass('page-template-media')){


    // ---------------------------------------------------------- VIDEO EMBED IN LIGHTBOX
    // ----------------------------------------------------------
    RwUtil.videoEmbed('image-container', 'swiper-slide-active');


    // ---------------------------------------------------------- MEDIA
    // ----------------------------------------------------------

    // RwSliders.carouselBullet('[comp="carousel-light"] .sw-carousel');


    var image = document.querySelectorAll('.image-container');

    for (var i = 0; i < image.length; i++) {
      image[i].addEventListener('click', function(e) {

        if(e.target.tagName == 'IMG'){
          var target = e.target.parentElement;
        } else {
          var target = e.target;
        }

        if(target.children[0] !== undefined && target.children[0].tagName == 'IMG'){

          var elem = target.parentElement.parentElement.querySelector('.blocco');

          basicLightbox.create(
            // '<section>'+ opInt +'</section>',
            elem.cloneNode(true).outerHTML,
            {
              className: 'fw-gallery media',
              onShow: (instance) => {
                // rimuovo padding bottom da section
                instance.element().querySelector('section.blocco');


                // chiusura a ESC
                document.onkeydown = (e) => {
                  if (e.keyCode == 27 && instance){
                    instance.close();
                  }
                }

                // X chiusura
                instance.element().querySelector('#swiper-fw-close').onclick = () => {
                  instance.close()
                }
              }
            }
          ).show();

          // var slideIndex = e.target.closest('.sw-carousel').querySelector('.swiper-slide-active').getAttribute('data-swiper-slide-index');
          // console.log('slideIndex: ' + slideIndex);
          RwSliders.carouselFw('.basicLightbox .sw-carousel', 1);
        }

      });
    }


      //---------------------------------------------------------
    // ON RESIZE
    $(window).resize(function() {

    });

  }
});
