
// ---------------------------------------------------------- SLIDER MAIN
// ----------------------------------------------------------
// usato in :
// templates/parts/acf-comp/slider-text-thumb.php -> [home#caratteristiche-prodotto]

var sliderMain = function (selector, space) {

  $(selector).each(function () {
    var ordinamento = $(this).closest('.blocco').data('row');

    if(typeof space == "undefined"){
      space = 0;
    }

    if($(this).closest('.blocco').hasClass('thumbson')){
      var galleryThumbs = new Swiper('.slider-thumb.row-' + ordinamento, {
        spaceBetween: 16,
        slidesPerView: 4,
        loop: true,
        loopedSlides: 4, //DEVONO ESSERE LE STESSE DI TRA THUMBS E GALLERY
        lazy: true,
        lazy: {
          loadPrevNext: true,
        },
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
      });
    }

    var galleryTop = new Swiper(this, {
      spaceBetween: space,
      loop: true,
      slidesPerView: 1,
      loopedSlides: 4, //DEVONO ESSERE LE STESSE DI TRA THUMBS E GALLERY
      autoHeight: false,
      centeredSlides: true,
      keyboard: {
        enabled: true,
        onlyInViewport: true,
      },
      lazy: true,
      lazy: {
        loadPrevNext: true,
        loadOnTransitionStart: true,
      },
      navigation: {
        nextEl: '.swiper-button-next.row-' + ordinamento,
        prevEl: '.swiper-button-prev.row-' + ordinamento,
      },
      thumbs: {
        swiper: galleryThumbs,
      },
    });

    if($(this).closest('.blocco').hasClass('thumbson')){
      galleryTop.controller.control = galleryThumbs;
    }

  });
};



// ---------------------------------------------------------- CAROUSEL BULLET
// ----------------------------------------------------------
// templates/parts/acf-comp/carousel.php -> [home#applicazione, single-prodotto#carosello]

var carouselBullet = function (selector) {

  $(selector).each(function () {
    var swid = $(this).closest('.blocco').attr('id');
    var ordinamento = $(this).closest('.blocco').data('row');

    var swiperCarousel = new Swiper(this, {
        centeredSlides: true,
        slidesPerView: 'auto',
        spaceBetween: 0,
        loop: true,
        lazy: true,
        lazy: {
          observer: true,
          loadPrevNext: true,
        },
        navigation: {
          nextEl: '.swiper-button-next.row-' + ordinamento + '[swid="'+ swid +'"]',
          prevEl: '.swiper-button-prev.row-' + ordinamento + '[swid="'+ swid +'"]',
        },
        keyboard: {
          enabled: true,
          onlyInViewport: true,
          pageUpDown: false,
        },
        pagination: {
          el: '.swiper-pagination.row-'  + ordinamento + '[swid="'+ swid +'"]',
          clickable: true,
        },
        on: {
          init: function () {
            var attr = $('#'+ swid +' .swiper-slide-active img').data('caption');
            $('#'+ swid +' .sw-command-bar .info-media').html(attr);
          },
          afterInit: function () {
            $('#'+ swid).css('opacity', '1');
          },
          slideChangeTransitionEnd: function () {
            var attr = $('#'+ swid +' .swiper-slide-active img').data('caption');
            $('#'+ swid +' .sw-command-bar .info-media').html(attr);
          }
        },
    });

    swiperCarousel.on('lazyImageReady', function(ordinamento){
      swiperCarousel.init();
      swiperCarousel.update();
    });

  });
};



// ---------------------------------------------------------- CAROUSEL FULL SCREEN
// ----------------------------------------------------------
// templates/parts/acf-comp/carousel.php -> [home#applicazione, single-prodotto#carosello]

var carouselFw = function (selector, index) {

  $(selector).each(function () {
    var swid = $(this).closest('.blocco').attr('id');
    var ordinamento = $(this).closest('.blocco').data('row');

    var $commandBar = $('.basicLightbox #'+ swid +' .sw-command-bar');
    var $infoMedia = $('.basicLightbox #'+ swid +' .sw-command-bar .info-media');
    var caption = $('.basicLightbox #'+ swid +' .swiper-slide-active img').data('caption');
    var $activeSlide = $('.basicLightbox #'+ swid + ' .swiper-slide-active');

    const swiperCarousel = new Swiper(this, {
        // initialSlide: index,
        centeredSlides: true,
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        observer: true,
        observeParents: true,
        // watchSlidesVisibility: true,
        breakpoints: {
          992: { // --- md
            slidesPerView: 1.75,
          },
        },
        lazy: true,
        lazy: {
          observer: true,
          loadPrevNext: true,
          // loadPrevNextAmount: 3,
        },
        navigation: {
          nextEl: '.swiper-button-next.row-' + ordinamento + '[swid="'+ swid +'"]',
          prevEl: '.swiper-button-prev.row-' + ordinamento + '[swid="'+ swid +'"]',
        },
        keyboard: {
          enabled: true,
          onlyInViewport: true,
          pageUpDown: false,
        },
        pagination: {
          el: '.swiper-pagination.row-'  + ordinamento + '[swid="'+ swid +'"]',
          clickable: true,
        },
        on: {
          init: function () {

            $infoMedia.html(caption);
            function sizeRuler(){
              // // top margin
              var headerHeight = $('header#wrapper-navbar').height();

              if($('body').hasClass('admin-bar')){
                var headerOffset = $('#wpadminbar').height() + headerHeight;
              } else {
                var headerOffset = headerHeight;
              }
              //
              // // var headerOffset = $('header#wrapper-navbar').offsetTop;
              // // var el = document.querySelector('header#wrapper-navbar');
              // // var headerOffset = window.pageYOffset + el.getBoundingClientRect().top;
              //
              $('.basicLightbox__placeholder').css('top', headerOffset );
              $('.basicLightbox__placeholder').css('max-height', 'calc(100% - ' + headerOffset + 'px)');
              // // calc(100% - 127.35px)
              // console.log('headerHeight: ' + headerHeight + ' headerOffset.top: ' + headerOffset);

              // command bar width
              $commandBar.css('max-width', ('calc('+ $activeSlide.width() +'px + (var(--bs-gutter-x)*6) )') );
              $commandBar.css('min-height', $infoMedia.outerHeight());
            }
            sizeRuler();

             //---------------------------------------------------------
            // ON RESIZE
            $(window).resize(function() {
              sizeRuler();

              // resize della command bar con le info media
              var $activeSlide = $('.basicLightbox #'+ swid + ' .swiper-slide-active');
              $commandBar.css('max-width', ('calc('+ $activeSlide.width() +'px + (var(--bs-gutter-x)*6) )') );
              $commandBar.css('min-height', $infoMedia.outerHeight());
            });

          },
          afterInit: function () {
            $('.basicLightbox #'+ swid).css('opacity', '1');
            // swiperCarousel.update();
          },
          slideChangeTransitionEnd: function () {
            var caption = $('.basicLightbox #'+ swid +' .swiper-slide-active img').data('caption');
            $('.basicLightbox #'+ swid +' .sw-command-bar .info-media').html(caption);
          },
          observerUpdate: function(){
            swiperCarousel.update();

            // resize della command bar con le info media
            setTimeout(function(){
              var $activeSlide = $('.basicLightbox #'+ swid + ' .swiper-slide-active');
              $commandBar.css('max-width', ('calc('+ $activeSlide.width() +'px + (var(--bs-gutter-x)*6) )') );
            }, 200);
          },
          update: function (){

          }
        },
    });



    swiperCarousel.on('lazyImageReady', function(ordinamento){
      swiperCarousel.init();
      swiperCarousel.update();
    });
  });
};



export {
  sliderMain,
  carouselBullet,
  carouselFw
};
